<template>
  <div>
    <v-card>
      <v-card-text>
        <v-row>
          <v-col cols="6">
            <mrc_ess_multiText :label="$t('label_groupName')+'*'"
                               class="mt-4"
                               :errorMessages="$errors('list','group_name',requiredRule)"
                               :isNational="judegNational('group_name')"
                               v-model="list.group_name"
                               :readonly="disabled">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="3"
                 class="mt-2">
            <mrc_ess_NumInput v-model="list.sort"
                              :readonly="disabled"></mrc_ess_NumInput>
          </v-col>
          <v-col cols="3"
                 class="mt-3">
          </v-col>
          <v-col cols="12">
            <mrc_ess_autoCompleteOrg v-model="list.company_uuid"
                                     :disabled="disabled"
                                     :errMsg="$errors('list','company_uuid',requiredRule)"
                                     :multiple='true'></mrc_ess_autoCompleteOrg>
          </v-col>
          <v-col cols="12">
            <h5 style="color:rgb(0, 0, 0, 0.38);font-size:20px;"
                class="mb-4 mt-10">{{$t('label_groupMember')}}</h5>
          </v-col>
          <v-col cols="12">
            <v-card class="mb-8"
                    style="border:1px solid #e0e0e0">
              <v-card-text>
                <v-col cols="12">
                  <mrc_ess_autoCompleteStaff v-model="list.staffsUuids"
                                             :disabled="disabled"
                                             @changeStaffList='changeStaff'
                                             :multiple='true'>
                  </mrc_ess_autoCompleteStaff>
                </v-col>
                <v-col cols="12">
                  <mrc_ess_autoCompleteDepartment v-model="list.levelsUuids"
                                                  :disabled="disabled"
                                                  @changeStaffList='changeDepartment'
                                                  :label="$t('label_department') + '*'"
                                                  :multiple="true"></mrc_ess_autoCompleteDepartment>
                </v-col>
                <v-col cols="12">
                  <mrc_ess_autoCompletePosition v-model="list.postsUuids"
                                                :disabled="disabled"
                                                @changeStaffList='changePosition'
                                                :multiple="true"></mrc_ess_autoCompletePosition>
                </v-col>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-card-text>
      <mrc_ess_submitBtn @success='success'
                         @cancel='cancel'
                         :loading='btnIsLoading'
                         v-if='!disabled'></mrc_ess_submitBtn>
    </v-card>

  </div>
</template>

<script> 
import {
  getStaffCompanyGroupList
  , addCompanyGroup
  , detailCompanyGroup
  , deleteCompanyGroup
} from '@/service/api/staffmsg.js'
import { getBusinessList } from '@/service/api/prompt.js'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      number: 0,
      list: {},
      btnIsLoading: false,
      changeStaffList: [],
      changePositionList: [],
      departmentList: []
    };
  },
  computed: {
  },
  created () { },
  watch: {
  },
  async mounted () {
    await this.national('mrbase_sys_group')
    if (this.$store.state.uuid) {
      await this.getDetail()
      this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },

  methods: {
    changeStaff (v) {
      this.changeStaffList = v
    },
    changePosition (v) {
      this.changePositionList = v
    },
    changeDepartment (v) {
      this.departmentList = v
    },
    paramsChange () { },
    async getDetail () {
      const res = await detailCompanyGroup({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
        console.log(this.list);
      }
      else {

      }
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      let changeStaffList = this.changeStaffList.map(item => {
        return { organization_uuids: item.uuid, organization_types: 1 }
      })
      let changePositionList = this.changePositionList.map(item => {
        return { organization_uuids: item.uuid, organization_types: 3 }
      })
      let departmentList = this.departmentList.map(item => {
        return { organization_uuids: item.value_field, organization_types: 2 }
      })
      let list = {
        group_name: this.list.group_name,
        group_type: 5,
        sort: this.list.sort,
        company_uuid: this.list.company_uuid,
        mrbase_sys_group_sub: [
          ...changeStaffList,
          ...changePositionList,
          ...departmentList,
        ],
      }
      console.log(changeStaffList,
        changePositionList,
        departmentList);
      return
      this.btnIsLoading = true
      if (this.$store.state.uuid) {
        addCompanyGroup({ ...this.$deletefield(null, list), uuid: this.$store.state.uuid }).then(async res => {
          if (res.code == 200) {
            this.$Message({ text: this.$t('message_The_operation_was_successful') })
            await this.getDetail()
            this.btnIsLoading = false
          }
          else {
            this.$Message({ text: this.$t('message_The_modification_failed') + res.msg, color: 'rgb(0,0,0,0.78)' })
            this.btnIsLoading = false
          }
        })
      }
      else {
        addCompanyGroup({ ...list }).then(async res => {
          if (res.code == 200) {
            this.$Message({ text: this.$t('message_added_successfully') })
            this.btnIsLoading = false
            this.$router.push({ name: 'staffGroup' })
          }
          else {
            this.btnIsLoading = false
            this.$Message({ text: res.msg, color: 'rgb(0,0,0,0.78)' })
          }
        })
      }
    },
    cancel () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_cancellation_of_the_edit')
      }).then(res => {
        this.$router.push({
          name: 'staffGroup'
        })
      }).catch(err => { })
    },
  },
  validations: {
    list: {
      group_name: {
        required
      },
      company_uuid: {
        required
      }
    }
  },
};
</script>

